'use client';

import { useState, useEffect } from 'react';

// --- SVG Icon Components ---

const MagicWandIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <path d="M15 4V2" />
    <path d="M15 10V8" />
    <path d="M12.5 7H17.5" />
    <path d="M20 5.5V2.5" />
    <path d="M4 15l4.9-4.9" />
    <path d="M13 22l4.9-4.9" />
    <path d="M2 22l2.5-2.5" />
    <path d="M19.5 6.5L22 4" />
    <path d="M9.8 14.2l-2.5 2.5" />
    <path d="M14.2 9.8l2.5-2.5" />
    <path d="M7.3 2.3L5.5 4" />
    <path d="M4 5.5L2.3 7.3" />
  </svg>
);

const DownloadIcon = () => (
  <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
    <path d="M21 15v4a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2v-4" />
    <polyline points="7 10 12 15 17 10" />
    <line x1="12" y1="15" x2="12" y2="3" />
  </svg>
);

// --- Main Page Component ---

export default function HomePage() {
  const [prompt, setPrompt] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [imageUrl, setImageUrl] = useState(null);
  const [isMounted, setIsMounted] = useState(false);

  // Trigger animations on component mount
  useEffect(() => {
    setIsMounted(true);
  }, []);

  const handleSubmit = async (event) => {
    event.preventDefault();
    setIsLoading(true);
    setError(null);
    setImageUrl(null);

    try {
      const response = await fetch('http://localhost:8000/api/generate-image', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ prompt }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.detail || 'An unknown error occurred');
      }

      const data = await response.json();
      const newImageUrl = `data:${data.mime_type};base64,${data.image_b64}`;
      setImageUrl(newImageUrl);

    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  };
  
  const handleDownload = () => {
    if (!imageUrl) return;
    const link = document.createElement('a');
    link.href = imageUrl;
    const fileName = prompt.substring(0, 30).replace(/\s+/g, '_') || 'generated_image';
    link.download = `${fileName}.png`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    // Main container with a deep, dark gradient background
    <div className="min-h-screen w-full bg-gradient-to-br from-[#111827] to-[#0c0d14] text-gray-100 flex justify-center items-center p-4 overflow-hidden">
      {/* Animated background shapes with the new golden accent */}
      <div className="absolute top-0 left-0 w-72 h-72 bg-amber-500 rounded-full mix-blend-screen filter blur-3xl opacity-10 animate-blob"></div>
      <div className="absolute top-0 right-0 w-72 h-72 bg-amber-400 rounded-full mix-blend-screen filter blur-3xl opacity-5 animate-blob animation-delay-2000"></div>
      <div className="absolute bottom-0 left-1/4 w-72 h-72 bg-amber-600 rounded-full mix-blend-screen filter blur-3xl opacity-5 animate-blob animation-delay-4000"></div>

      <main className={`container mx-auto w-full max-w-2xl flex flex-col gap-8 transition-all duration-1000 ease-in-out ${isMounted ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
        
        <header className="text-center">
          {/* Header text with the new golden gradient */}
          <h1 className="text-4xl sm:text-5xl lg:text-6xl font-extrabold tracking-tight bg-clip-text text-transparent bg-gradient-to-r from-amber-300 to-amber-500">
            Gemini Image Generator
          </h1>
          <p className="text-gray-500 mt-3 text-base sm:text-lg">
            Transform your imagination into stunning visuals with AI.
          </p>
        </header>

        <div className="relative">
          {/* Glowing border effect with the new accent color */}
          <div className="absolute -inset-0.5 bg-gradient-to-r from-amber-600 to-yellow-500 rounded-2xl blur opacity-40 transition duration-1000 animate-tilt"></div>
          {/* Main form container with a dark, semi-transparent background */}
          <div className="relative bg-[#1f2937]/60 p-6 rounded-2xl shadow-2xl border border-white/10 backdrop-blur-lg">
            <form onSubmit={handleSubmit} className="w-full flex flex-col gap-4">
              <textarea
                value={prompt}
                onChange={(e) => setPrompt(e.target.value)}
                placeholder="e.g., A cinematic photo of Rahul on a Goa beach at sunset"
                className="w-full p-4 bg-gray-900/70 border-2 border-gray-700 rounded-xl focus:ring-2 focus:ring-amber-500 focus:border-amber-500 focus:outline-none transition-all duration-300 text-base placeholder-gray-500 text-gray-100"
                rows="3"
                required
              />
              <button
                type="submit"
                disabled={isLoading}
                className="w-full flex items-center justify-center gap-3 bg-gradient-to-r from-amber-500 to-amber-600 hover:from-amber-600 hover:to-amber-700 disabled:from-gray-600 disabled:to-gray-700 disabled:cursor-not-allowed text-gray-900 font-bold py-3 px-4 rounded-xl transition-all duration-300 ease-in-out transform hover:scale-[1.03] active:scale-100 shadow-lg shadow-amber-900/50"
              >
                {isLoading ? (
                  <svg className="animate-spin h-5 w-5 text-gray-900" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                ) : (
                  <MagicWandIcon />
                )}
                <span>{isLoading ? 'Conjuring...' : 'Generate'}</span>
              </button>
            </form>
          </div>
        </div>

        <div className="w-full min-h-[300px] flex items-center justify-center transition-all duration-500">
          {error && (
            <div className="bg-red-900/50 border border-red-700 text-red-200 px-4 py-3 rounded-xl text-center w-full animate-fade-in">
              <p><strong>Error:</strong> {error}</p>
            </div>
          )}

          {isLoading && (
            <div className="w-full text-center flex flex-col items-center gap-4 animate-fade-in">
              <div className="w-full max-w-lg h-80 bg-gray-800/50 rounded-xl animate-pulse border border-white/10"></div>
              <p className="text-gray-500">Summoning pixels from the digital ether...</p>
            </div>
          )}

          {imageUrl && !isLoading && (
            <div className="w-full flex flex-col gap-4 items-center animate-fade-in">
              <div className="w-full bg-black/30 p-4 rounded-2xl border border-white/10 shadow-2xl backdrop-blur-md">
                <img 
                  src={imageUrl} 
                  alt={prompt}
                  className="rounded-xl w-full h-auto object-contain"
                />
              </div>
              <button
                onClick={handleDownload}
                className="flex items-center justify-center gap-2 bg-gray-800/50 hover:bg-gray-700/50 border border-gray-700 text-gray-300 font-semibold py-2 px-6 rounded-lg transition-all duration-300 backdrop-blur-md"
              >
                <DownloadIcon />
                <span>Download</span>
              </button>
            </div>
          )}
        </div>
      </main>
    </div>
  );
}
